.spinner-outer {
  position: fixed;
  top: 0;
  background-color: rgba(0,0,0,0.6);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100vh;
  width: 100vw;
}

.spinner {
  width: 3em;
  height: 3em;
  -webkit-animation: loaderAnim 1.25s infinite ease-in-out;
          animation: loaderAnim 1.25s infinite ease-in-out;
  outline: 1px solid transparent;
}

.spinner_largeBox {
  height: 3em;
  width: 3em;
  background-color: green;
  outline: 1px solid transparent;
  position: fixed;
}

.spinner_smallBox {
  height: 3em;
  width: 3em;
  background-color: black;
  position: fixed;
  z-index: 1;
  outline: 1px solid transparent;
  -webkit-animation: smallBoxAnim 1.25s alternate infinite ease-in-out;
          animation: smallBoxAnim 1.25s alternate infinite ease-in-out;
}

@-webkit-keyframes smallBoxAnim {
  0% {-webkit-transform: scale(0.2);transform: scale(0.2);}
  100% {-webkit-transform: scale(0.75);transform: scale(0.75);}
}

@keyframes smallBoxAnim {
  0% {-webkit-transform: scale(0.2);transform: scale(0.2);}
  100% {-webkit-transform: scale(0.75);transform: scale(0.75);}
}

@-webkit-keyframes loaderAnim {
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100% {-webkit-transform: rotate(90deg);transform: rotate(90deg);}
}

@keyframes loaderAnim {
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100% {-webkit-transform: rotate(90deg);transform: rotate(90deg);}
}


